import { BaseConnectorConfig } from '@xip-online-data/types';
import { FileSourceConfig } from './types';

export function generateCollectionName(
  connectorConfig: BaseConnectorConfig,
  sourceConfig: FileSourceConfig,
): string {
  return `${connectorConfig.datasourceIdentifier}_${sourceConfig.ftpIdentifier}`;
}

export function generateKafkaTopic(
  connectorConfig: BaseConnectorConfig,
  sourceConfig: FileSourceConfig,
): string {
  return `${connectorConfig.tenantIdentifier}_SOURCE_${connectorConfig.datasourceIdentifier}_${sourceConfig.ftpIdentifier}`;
}
